import React from 'react';

// importing the default components
import Container from "reactstrap/es/Container";
import Form from "reactstrap/es/Form";
import Col from "reactstrap/es/Col";
import Row from "reactstrap/es/Row";
import FormGroup from "reactstrap/es/FormGroup";
import Input from "reactstrap/es/Input";
import Label from "reactstrap/es/Label";


/**
 * Default class signature.
 */
export default class ApplicationForm extends React.Component {
    /**
     * Default class constructor.
     *
     * @param props
     */
    constructor(props) {
        super(props);
        this.state = {
            id: 7, // here you change the ID according the application form sent by email.
            name: "",
            email: "",
            comments: "",
            token: "",
            resume: null,
        };

        // binding the default components functions.
        this.fetchToken = this.fetchToken.bind(this);
    }

    componentWillMount() {
        const { id = null } = this.state;

        this.fetchToken(id);
    }

    fetchToken(id= null) {
        // collect the form data while iterating over the inputs
        var data = JSON.stringify({
            "posting": 7
        });

        var xhr = new XMLHttpRequest();
        xhr.withCredentials = true;

        xhr.addEventListener("readystatechange", function () {
            if (this.readyState === 4) {
                console.log(this.responseText);
            }
        });

        xhr.open("POST", "https://app.applybyapi.com/gentoken/");
        xhr.setRequestHeader("Content-Type", "application/json");
        xhr.setRequestHeader("cache-control", "no-cache");
        xhr.setRequestHeader("Postman-Token", "385955f3-3ab0-45eb-a5b8-ba09e99b5654");

        xhr.send(data);
    }

    render() {
        return (
            <Container>
                <h3>
                    Welcome to redilly application form.
                </h3>
                <Form className={'my-form'}>
                    <Row>
                        <Col>
                            <FormGroup>
                                <Label>
                                    Name
                                </Label>
                                <Input
                                    type={'text'}
                                    name={'name'}
                                    id={'name'}
                                    value={''}
                                />
                            </FormGroup>
                        </Col>
                    </Row>
                </Form>
            </Container>
        );
    }
}